@import "../../global.scss";

.iskustvo {
    background-color: $pozadina_bela;
    transform: translateY(-80px);
    border-radius: 2rem;
    box-shadow: 0px 5px 15px 5px #caccd398;
    width: 40%;
    padding: 2rem;
    min-height: 10rem;
    @include box-center(column, space-around);
    align-items: flex-start;

    .iskustvo-sadrzaj {
        font-family: "Roboto";
        font-weight: 400;
        font-size: 1.25rem;
        color: #10151d;
        text-align: center;
        padding: 2rem 0;
    }

    .iskustvo-footer {
        @include box-center(row, flex-start);

        .iskustvo-slika {
            border-radius: 50%;
            @include fit-image;
            width: 3rem;
            height: 3rem;
            margin-right: 1rem;
        }

        .iskustvo-autor {
            font-family: "Roboto";
            font-weight: 400;
            font-size: 1rem;
            color: #767676;
            padding: 1rem 0;
            p:first-child {
                margin-bottom: 0.5rem;
            }
            p:last-child {
                font-size: 0.8rem;
            }
        }
    }
}

@media screen and (max-width: 800px) {
    .iskustvo {
        width: 25rem;
        margin-bottom: 2rem;
    }
}

@media screen and (max-width: 550px) {
    .iskustvo {
        width: 20rem;
        margin-bottom: 2rem;
    }
}
